
@charset "UTF-8";
.page {
  height: 100vh;
  /*相对视窗*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*主轴为列*/
  background-color: #eaebec;
}
.page > main {
    -ms-flex-positive: 1;
        flex-grow: 1;
    /*主轴上伸缩项目按照比例占据剩余空间。*/
}
.page > main {
    max-width: 1440px;
    min-width: 1024px;
    margin-top: 16px;
    margin-bottom: 16px;
    padding: 0 16px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    /*注意这里的main也为容器，默认主轴为X方向。伸缩项目平均分布，剩余空间宽度值为项目间距一半*/
    width: 100%;
    /*如果没有这句main宽度就是1024px。自动收缩*/
    -ms-flex-item-align: center;
        align-self: center;
    /*注意别看错了，让整个main居中。写在伸缩项目中，可覆盖父层align-items属性。*/
}
.page #resume-editor {
    background-color: #fff;
    min-width: 35%;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
}
.page #resume-preview {
    -ms-flex-positive: 1;
        flex-grow: 1;
    /*位于主轴的项目进行拉伸。上级盒子新定义了flex,默认主轴方向为横向。*/
    margin-left: 16px;
    background-color: #fff;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
}
svg.icon {
  height: 1em;
  width: 1em;
  fill: currentColor;
  vertical-align: -0.1em;
  font-size: 16px;
}



