
@charset "UTF-8";
#resume-preview {
  background: white;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  padding: 2em;
  /*所有字体间距调整都用em，方便控制。*/
  color: #333;
  line-height: 1.2;
  overflow: auto;
}
#resume-preview * {
    box-sizing: border-box;
    font-variant: normal;
    font-weight: normal;
    /*将标题文字的bold变为normal。文字变细*/
}
#resume-preview ol {
    list-style: none;
}
#resume-preview section + section {
    margin-top: 2em;
    /*这样的话，最上面一个就不会有margin-top，避免了外边距合并的问题*/
}
#resume-preview p {
    white-space: pre-line;
}
#resume-preview section > h2:first-child {
    background: #ddd;
    display: inline-block;
    padding: .2em;
    margin-bottom: .5em;
}
#resume-preview section[data-name="profile"] > h1 {
    margin: .1em 0;
    font-size: 4em;
}
#resume-preview section[data-name="workHistory"] li + li {
    margin-top: 1em;
}
#resume-preview section[data-name="workHistory"] li h3 {
    border-bottom: 1px solid #999;
    padding-bottom: .3em;
    margin-bottom: .3em;
}
#resume-preview section[data-name="education"] li {
    line-height: 1.5;
}



